import React, { useContext, useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import { LogingedContext } from "../../App";

const Detail = () => {
  const {SERVER_IP} = useContext(LogingedContext);
  // .
  const { id } = useParams(); //

  const [board, setBoard] = useState({
    id: "",
    title: "",
    content: "",
    member: {},
    regDate:""
  });

  const navigator = useNavigate();
  useEffect(() => {
    axios
      .get(SERVER_IP+"/boards/" + id, {
        headers: {
          Authorization: localStorage.getItem("Authorization"),
        },
      })
      .then((res) => {
        setBoard(res.data);
      })
      .catch((err) => {
          errFun(err);
      });
  }, []);

  //  
  const errFun = (err) => {
    if (err.response.status === 403) {
      alert(" .");
    } else {
      let errMessage = " = " + err.response.data.type + "\n";
      errMessage += err.response.data.title + "\n";
      errMessage += err.response.data.status + "\n";
      errMessage += err.response.data.instance + "\n";
      errMessage += err.response.data.timestamp;
      alert(errMessage);
    }

    navigator("/");
  };

  //
  const deleteBoard = (id) => {
    axios({
        method:"DELETE",
        headers: {
        Authorization: localStorage.getItem("Authorization"),
        },
        url : SERVER_IP+"/boards/"+id,
        })
        .then((res)=>{
        if(res.data ==="ok")
             navigator("/") ;
        else
         alert(" .");
     })
    .catch((err)=>{
         errFun(err);
    });       
  };//

  // 
  const updateBoard = () => {
    navigator("/updateForm/"+id);

  };

  return (
    <div>
      <h1></h1>
      <Button
        variant="warning"
        onClick={updateBoard}
        disabled={
            board.member.name === localStorage.getItem("name") ? false : true
        }
      >
        
      </Button>
       {"     "}
      <Button
        variant="danger"
        onClick={() => deleteBoard(board.id)}
        disabled={board.member.id === localStorage.getItem("id") ? false : true}
      >
        
      </Button>
      <hr />
      <h2> : {board.id}</h2>
      <h2> : {board.title}</h2>
      <h2> : {board.member.name}</h2>
      <h2> : {board.content}</h2>
      <h2> : {board.regDate}</h2>
    </div>
  );
};

export default Detail;
