import React, { useContext, useState } from "react";
import { Button } from "react-bootstrap";
import Form from "react-bootstrap/Form";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { LogingedContext } from "../../App";

const LoginForm = () => {
    let {isLoggedIn ,onLoggedChange, SERVER_IP}= useContext(LogingedContext);
    console.log("logingedCon = " , isLoggedIn)
    console.log("onLoggedChange = " , onLoggedChange)
    console.log("SERVER_IP = " , SERVER_IP)
    
  //   username, password   useState
  const [member, setMember] = useState({
    username: "",
    password: "",
  });

  // input       
  const changeValue = (e) => {
    setMember({
      ...member,
      [e.target.name]: e.target.value,
    });
  }; //

  const navigator = useNavigate();

  const submitLogin = (e) => {
    

    e.preventDefault();//submit  

    let formData = new FormData(); //  
    formData.append("username", member.username);
    formData.append("password", member.password);

    axios({
        method:"POST",
        url : SERVER_IP+"/login",
        data : formData,
    })
    .then((res)=>{
        console.log("res = " , res)

         //   
        localStorage.setItem("memberNo", res.data.memberNo);
        localStorage.setItem("id", res.data.id);
        localStorage.setItem("name", res.data.name);
        localStorage.setItem("address", res.data.address);
        localStorage.setItem("Authorization", res.headers.authorization);
       
         //  - cookie

        //App.js  isLoggedIn  true .
        onLoggedChange(true);

        navigator("/"); 

    }).catch((err)=>{
        alert("  ~^^. err " + err);
        setMember({
        username :'' , 
        password:''
      })

    });
     
  }


  return (
    <div>
      <h3 style={{ padding: "10px" }}> </h3>
      <Form onSubmit={submitLogin}>
        <Form.Label htmlFor="username"></Form.Label>
        <Form.Control
          type="text"
          id="username"
          name="username"
          onChange={changeValue}
          value={member.username}
        />
        <Form.Label htmlFor="password"></Form.Label>
        <Form.Control
          type="password"
          id="password"
          name="password"
          onChange={changeValue}
          value={member.password}
        />
        <p />
        <Button variant="primary" type="submit">
          
        </Button>
        <p />
      </Form>
    </div>
  );
};
export default LoginForm;
